/* stylelint-disable plugin/selector-bem-pattern */
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none;
  color: inherit; }

img {
  max-width: 100%;
  height: auto; }

button {
  cursor: pointer; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font: normal 400 16px/1.2 "Open Sans", "Arial", sans-serif;
  color: #363636;
  background-color: #ffffff; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  position: relative;
  margin: 0 25px; }
  @media (min-width: 768px) {
    .container {
      margin: 0 50px; } }
  @media (min-width: 1300px) {
    .container {
      width: 1180px;
      margin: 0 auto; } }

.button {
  transition: opacity 0.3s ease-in;
  display: inline-block;
  padding: 13px 42px 16px 32px;
  font: normal 700 16px/1.2 "Open Sans", "Arial", sans-serif;
  color: #363636;
  border: 1px solid #d8d8d8;
  border-radius: 25px;
  background: center right 28px/7px 17px no-repeat url("../img/arrow.svg"), transparent; }
  .button:hover {
    opacity: 0.7; }
  .button:active {
    opacity: 0.3; }

.article {
  transition: opacity 0.3s ease-in;
  position: relative;
  width: 100%;
  font: normal 400 16px/1.2 "Open Sans", "Arial", sans-serif;
  color: #363636;
  text-align: left;
  border: 1px solid #d8d8d8;
  background-color: #f8f8f8; }
  .article:hover {
    opacity: 0.7; }
  .article:active {
    opacity: 0.3; }

.article__link-wrapper {
  display: block; }

.article__photo-wrapper {
  overflow: hidden;
  position: relative;
  min-height: 100px; }
  .article__photo-wrapper picture,
  .article__photo-wrapper img {
    display: block;
    width: 100%;
    height: auto;
    margin: 0;
    padding: 0; }
  .article__photo-wrapper::after {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 70%;
    padding: 10px 10px 6px;
    font: normal 700 12px/1.2 "Open Sans", "Arial", sans-serif;
    color: #ffffff;
    background-color: #006f79; }
  .article__photo-wrapper--independent::after {
    content: "Independent living";
    background-color: #006f79; }
  .article__photo-wrapper--support::after {
    content: "Restaurant & Support available";
    padding: 10px 8px 6px;
    background-color: #ec6608; }

.article__container {
  margin: 15px 20px 18px; }

.article__title {
  margin: 22px 0 0;
  font: normal 700 20px/1.2 "Open Sans", "Arial", sans-serif; }

.article__address {
  margin: 7px 0 18px;
  font: normal 400 16px/1.2 "Open Sans", "Arial", sans-serif; }

.article__text {
  margin: 0;
  font: normal 400 16px/1.2 "Open Sans", "Arial", sans-serif; }

.article__price {
  font: normal 700 16px/1.2 "Open Sans", "Arial", sans-serif; }

.article__info {
  margin: 10px 0 0;
  font: normal 400 14px/1.2 "Open Sans", "Arial", sans-serif; }

.realty {
  position: relative;
  padding: 25px 0;
  font: normal 400 16px/1.2 "Open Sans", "Arial", sans-serif;
  color: #363636;
  text-align: center;
  background-color: #ffffff;
  overflow-x: hidden; }

.realty__title {
  width: 70%;
  margin: 27px auto;
  font: normal 700 36px/1.2 "Open Sans", "Arial", sans-serif;
  color: #45852d;
  text-align: center; }
  @media (max-width: 767px) {
    .realty__title {
      width: 100%; } }

.realty__list {
  display: flex;
  flex-flow: row wrap;
  width: calc(100% + 20px);
  margin: 40px -20px 15px 0;
  overflow-x: hidden; }
  @media (max-width: 1299px) {
    .realty__list {
      width: calc(100% + 2%);
      margin: 40px -2% 15px 0; } }
  @media (max-width: 767px) {
    .realty__list {
      display: block;
      width: 100%;
      margin: 30px 0 15px; } }

.realty__item {
  display: flex;
  width: 380px;
  margin: 0 20px 20px 0; }
  @media (max-width: 1299px) {
    .realty__item {
      width: 31%;
      margin: 0 2% 2% 0; } }
  @media (max-width: 767px) {
    .realty__item {
      width: 100%;
      max-width: 380px;
      margin: 0 auto 20px; } }

.realty__more {
  margin: 15px; }

.realty__storage {
  display: none; }
